#search-box {
  font: inherit;
  font-size: 1rem;
  padding: 0.6rem;
  max-width: 320px;
  margin-bottom: 1rem;
  border: 2px solid #ccc;
  border-radius: 4px;
  
}

.card {
  padding: 0.6rem;
  margin: 10px;
  min-width: 300px;
  max-width: 400px;
  background-color: rgba(50, 50, 50, 0.2);
  border-radius: 4px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.card-header {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.7rem;
}

.card-text {
  font-style: italic;
  margin-top: 0;
  margin-bottom: 0.6rem;
}
.card-text span {
  font-weight: bold;
}

.country-list {
display: flex;
flex-direction: column;
  gap: 0.3rem;
}

.list-item {
  display: flex;
  align-items: center;
  justify-content: start;
  gap: 0.5rem;
  padding: 0 0.6rem;
  background-color: rgba(50, 50, 50, 0.2);
  border-radius: 4px;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.list-item.hovered {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
  color: #fff;
    transform: scale(1.2);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.list-item img {
  height: 30px;
  width: 40px;
}

/* ----------------------------------------------------------- */
/* STYLES FOR MAIN */

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  min-height: 300px;
}

/* ----------------------------------------------------------- */

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
  line-height: 1.5;
  color: #2a2a2a;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input {
  font: inherit;
}

ul {
  list-style: none;
  padding: 0;
}

/* STYLES FOR BUTTON  */
button {
  margin: 0.25rem;
  /* padding: 8px 12px; */
  font-size: 1rem;
  font-family: inherit;
  cursor: pointer;
}

/* STYLES FOR HIDDEN  */
.hidden {
  display: none;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BACK BTN */
.back-btn {
  font-size: 1rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  border-radius: 0.5rem;
  color: #fff;
  background-color: #f68768;
  box-shadow: 0 2px 5px rgba(211, 37, 37, 0.7);
  transition: all 0.2s ease-in-out;
  transition: transform 0.2s ease-out, box-shadow 0.2s ease-out;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 6px rgba(22, 155, 175, 0.7);
}

.back-btn:active {
  transform: translateY(0);
  box-shadow: none;
}

.back-btn:disabled {
  background-color: #5f5f5f;
  transform: translateY(0);
  box-shadow: none;
}

.back-btn__link {
  display: flex;
  align-items: center;
  padding: 0.7rem 1.5rem;
  text-decoration: inherit;
  color: inherit;
}

.back-btn .icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: inherit;
}

/* ----------------------------------------------------------- */
/* STYLES FOR HEADING */

.page-header {
  font-family: 'Poppins', sans-serif;
  font-size: 3.5rem;
  font-weight: 600;
  color: #333;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  text-align: center;
  margin-bottom: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
}

.page-header::after {
  content: '';
  display: block;
  width: 100%;
  height: 0.2rem;
  background-color: #5f5f5f;
  margin-top: 1.5rem;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

/* ----------------------------------------------------------- */
/* STYLES FOR CONTENT */

.content {
  padding: 1.6rem;
  max-width: 30rem;
}

.content__header {
  font-size: 2rem;
  font-weight: bold;
  padding-left: 2rem;
  margin-bottom: 1rem;
}

.content__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.content__item {
  margin-bottom: 0.5rem;
}

.content__link {
  display: block;
  padding: 0.5rem;
  color: #333;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.content__link:hover {
  color: #4ae4c2;
  background-image: linear-gradient(to right, #fc354c, #0abfbc);
  transform: translateX(10px);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

/* ----------------------------------------------------------- */
/* STYLES FOR FOOTER */

.footer {
  color: #333;
  margin: 2rem 0;
  text-align: center;
  font-size: 0.8rem;
}

.footer::before {
  content: '';
  display: block;
  width: 100%;
  height: 0.2rem;
  background-color: #5f5f5f;
  margin-bottom: 3rem;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

.footer__container {
  padding: 0 1rem;
}

/* ----------------------------------------------------------- */
/* STYLES FOR SOCIALS */

.social__list {
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 0.8rem;
}

.social__item {
  width: 2.75rem;
  height: 2.75rem;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background-color: rgba(255, 255, 255, 0.3);
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: all 0.3s ease;
}

.social__link:hover,
.social__link:focus {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
  color: #fff;
  transform: scale(1.2);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
}

.social__icon {
  fill: currentColor;
  width: 45%;
  height: 45%;
}

/* ----------------------------------------------------------- */
/* STYLES FOR BTN TO TOP */

.to-top-btn {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  width: 2.75rem;
  height: 2.75rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.to-top-btn:hover {
  background-image: linear-gradient(to right, #0abfbc, #fc354c);
}

.to-top-btn__icon {
  /* display: block; */
  /* margin: 0 auto; */
  width: 100%;
  height: 100%;
  fill: currentColor;
  transition: transform 0.3s ease-in-out;
}

.to-top-btn:hover .to-top-btn__icon {
  transform: scale(2);
}


/* ----------------------------------------------------------- */